import ApiService from "@/core/services/ApiService";
import IBlock from "@/store/modules/block/interface/block.interface"; 
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class ConcessionaryService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/concessionary`, httpOptions());
  }

  getDataBySede(sedeId: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/concessionary/sede/${sedeId}`, httpOptions());
  }

  update(id: any, data: any){
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/concessionary/${id}`, data, httpOptions());
  }
}
