
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import IConcessionary from "@/store/modules/concessionary/interface/concessionary.interface"
import ISite from "@/store/modules/site/interface/site.interface"
import ConcessionaryService from "@/store/modules/concessionary/service/concessionary.service";
import SiteService from "@/store/modules/site/service/site.service";
import BannerService from "@/store/modules/banner/service/banner.service";
import IBanner from "@/store/modules/banner/interface/banner.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect'
import * as validationHelper from "@/store/helpers/validation.helper";

export default defineComponent({
  name: "BannersCreate",
  components: {
	  Multiselect
  },
  setup() {
    	const router = useRouter();
		const show = ref<Boolean>(false);

		const concessionaryService = new ConcessionaryService();
		const siteService = new SiteService();
		const bannerService = new BannerService();

		const concessionaries = ref<Array<IConcessionary>>([]);
		const sites = ref<Array<ISite>>([]);
		let file : any = null;

		const banner = ref<IBanner>({
			id: null,
			name: '',
			file: null,
			concessionary_id: null,
			site_id: null,
			duration: 0,
			visible: false
		});

		const getConcessionaries = (sedeId) => concessionaryService.getDataBySede(sedeId)
		.then((response) => {
			concessionaries.value = [...[{id: null, name: 'Todos los concesionarios'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const getSites = () => siteService.getData()
		.then((response) => {
			sites.value = [...[{id: null, name: 'Todas las sedes'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const validateForm = () => {
			let disabled = false;
			if(banner.value.name.length == 0 || !banner.value.duration || isNaN(banner.value.duration) || !file) disabled = true;
			return disabled
		}

		const changeFile = (e: any) => {
          if(e.target.files.length > 0){
            file = e.target.files[0]
          }
        }

		const saveData = () => {
			Swal.fire({
				title: 'ConfirmaciÃ³n!',
				text: "Â¿Esta seguro que desea guardar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						bannerService.create(groupData())
						.then((response) => {
							Swal.fire(
							'ConfirmaciÃ³n!',
							'InformaciÃ³n guardada exitosamente',
							'success'
							).then(result => {
								router.push({ name: "blocks-banners" });
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los datos',
							'error'
							);
						});
					}
			})
			
		}

		const groupData = () => {
			let formData = new FormData();
			formData.append('file', file);
			formData.append('name', banner.value.name);
			formData.append('visible', banner.value.visible?'1':'0');
			formData.append('duration', banner.value.duration.toString());
			formData.append('concessionary_id', banner.value.concessionary_id ? banner.value.concessionary_id.toString():'');
			formData.append('site_id', banner.value.site_id ? banner.value.site_id.toString():'');
			return formData;
		}

		const changeSite = (e) => {
			if(e && e != ''){
				let siteName = ''
				sites.value.forEach(site =>{
					if(site.id == e){
						siteName = site.name
					}
				})
				getConcessionaries(siteName)
			}
			else{
				concessionaries.value = []
				banner.value.concessionary_id = null
				banner.value.site_id = null
			}
		}

		onMounted(() => {
			getSites()
		});

		return {
            concessionaries,
			sites,
			getConcessionaries,
			getSites,
			validateForm,
			changeFile,
			saveData,
			changeSite,
			banner,
			show,
			validationHelper
        };
  },
});
